@import "../helpers/variables";

body {
  margin: 0;
  background: $background;
}

main {
  scroll-snap-type: y;
}

img {
  height: auto;
  max-width: 100%;
  width: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

figure {
  margin: 0;
}

button {
  margin: 0;
  background: none;
}
